import { useNavigate } from "react-router-dom";
import { MenuItem } from "primereact/menuitem";

export const useMenuItems = (menu: any, menuButton: any): MenuItem[] => {
  const navigate = useNavigate();

  const redirect = (e: any) => {
    if (menu.current) {
      const el = menu.current as any;
      el.toggle(menuButton.current);
    }

    navigate(e.item.target);
  };

  const items: MenuItem[] = [
    {
      label: "Marketing",
      icon: "fas fa-inbox",
      items: [
        {
          label: "Accounts",
          icon: "fas fa-building",
          target: "/accounts",
          command: redirect,
        },
        {
          label: "Contacts",
          icon: "fas fa-envelope",
          target: "/contacts",
          command: redirect,
        },
        {
          label: "Leads",
          icon: "fas fa-hand-point-right",
          target: "/leads",
          command: redirect,
        },
        {
          label: "Opportunities",
          icon: "fas fa-line-chart",
          target: "/opportunities",
          command: redirect,
        },
        {
          label: "Campaigns",
          icon: "fas fa-flag",
          target: "/campaigns",
          command: redirect,
        },
      ],
    },
    {
      label: "Inventory",
      icon: "fa-brands fa-dropbox",
      items: [
        {
          label: "Products",
          icon: "fa-brands fa-product-hunt",
          target: "/products",
          command: redirect,
        },
        {
          label: "Invoices",
          icon: "fas fa-file-invoice-dollar",
          target: "/invoices",
          command: redirect,
        },
        {
          label: "Sales Orders",
          icon: "fas fa-arrow-circle-up",
          target: "/salesorders",
          command: redirect,
        },
        {
          label: "Purchase Orders",
          icon: "fas fa-arrow-circle-down",
          target: "/purchaseorders",
          command: redirect,
        },
        {
          label: "Vendors",
          icon: "fas fa-truck",
          target: "/vendors",
          command: redirect,
        },
        {
          label: "GL Accounts",
          icon: "fas fa-list-ol",
          target: "/glaccounts",
          command: redirect,
        },
        {
          label: "Manufacturers",
          icon: "fa-brands fa-steam-square",
          target: "/manufacturers",
          command: redirect,
        },
        {
          label: "Product Categories",
          icon: "fas fa-sitemap",
          target: "/productcategories",
          command: redirect,
        },
        {
          label: "Tax Management",
          icon: "fas fa-percent",
          target: "/taxes",
          command: redirect,
        },
        {
          label: "Usage Units",
          icon: "fas fa-balance-scale",
          target: "/usageunits",
          command: redirect,
        },
      ],
    },
    {
      label: "Administration",
      icon: "fas fa-cog",
      items: [
        {
          label: "Currencies",
          icon: "fas fa-money-bill-1",
          target: "/currencies",
          command: redirect,
        },
        {
          label: "Roles",
          icon: "fas fa-id-badge",
          target: "/roles",
          command: redirect,
        },
        {
          label: "Users",
          icon: "fas fa-users",
          target: "/users",
          command: redirect,
        },
      ],
    },
  ];

  return items;
};
