import React, { useEffect, useRef, useState } from "react";
import { Outlet } from "react-router-dom";
import { Button } from "primereact/button";
import { Image } from "primereact/image";
import { SlideMenu } from "primereact/slidemenu";
import { useMenuItems } from "./useMenuItems";
import { useNavigate } from "react-router-dom";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import Logo from "../../assets/logo.png";

const Home: React.FC = () => {
  const [fullName, setFullName] = useState("Joe Doe");
  const menu = useRef(null);
  const menuButton = useRef(null);
  const navigate = useNavigate();
  const items = useMenuItems(menu, menuButton);
  const [profile, isProfile] = useAppSelector(
    (state: IAppState) => [state.users.profile, state.users.isProfile],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (isProfile) {
      setFullName(`${profile.firstName} ${profile.lastName}`);
    }
  }, [isProfile, profile]);

  const onMenuClick = (e: any) => {
    if (menu.current) {
      const el = menu.current as any;
      el.toggle(e);
    }
  };

  const onLogoutClick = () => {
    dispatch(userActions.signOutUser());
    navigate("/login");
  };

  return (
    <>
      <div className="grid top-bar">
        <div className="col top-padding">
          <Button
            type="button"
            onClick={onMenuClick}
            ref={menuButton}
            tooltip="Menu"
          >
            <i className="fa-solid fa-bars"></i>
          </Button>
        </div>
        <div className="col hidden-xs">
          <Image src={Logo} width="135" alt="Logo" />
        </div>
        <div className="col text-center top-padding">Welcome {fullName}</div>
        <div className="col text-right top-padding">
          <Button type="button" onClick={onLogoutClick} tooltip="Sign out">
            <i className="fas fa-sign-out"></i>
          </Button>
        </div>
      </div>
      <div>
        <SlideMenu
          ref={menu}
          model={items}
          popup
          viewportHeight={520}
          menuWidth={200}
          baseZIndex={10000}
        ></SlideMenu>
      </div>
      <main>
        <div className="grid">
          <div className="col-1">&nbsp;</div>
          <div className="col-10">
            <Outlet />
          </div>
          <div className="col-1">&nbsp;</div>
        </div>
      </main>
    </>
  );
};

export default Home;
